import React from 'react';
import PropTypes from 'prop-types';

/**
 * Render a Funnelback result as a module card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function finder__results__module(props) {
    const location = props.details.metaData.location &&
            props.query.facets.location && (
                <p className="finder__results__card__tag">
                    <span
                        className="fas fa-fw fa-map-marker-alt icon"
                        aria-hidden="true"
                    />{' '}
                    <span>Location:</span>{' '}
                    <span>{props.details.metaData.location}</span>
                </p>
            ),
        school = props.details.metaData.school && (
            <p className="finder__results__card__tag">
                <span
                    className="fas fa-fw fa-university icon"
                    aria-hidden="true"
                />{' '}
                <span>School:</span>{' '}
                <span>{props.details.metaData.school}</span>
            </p>
        ),
        topic = props.details.metaData.related && (
            <p className="finder__results__card__description">
                {props.details.metaData.topic}
            </p>
        ),
        department = props.details.metaData.department && (
            <p className="finder__results__card__tag">
                <span
                    className="fas fa-fw fa-building icon"
                    aria-hidden="true"
                />{' '}
                <span>Department:</span> {props.details.metaData.department}
            </p>
        ),
        specification = props.details.metaData.specification && (
            <p className="finder__results__card__tag">
                <span className="far fa-fw fa-pdf icon" aria-hidden="true" />{' '}
                <span>Download:</span>{' '}
                <a href={props.details.metaData.specification}>
                    Module specification
                </a>
            </p>
        );

    return (
        <li className="finder__results__card finder__results__contact">
            <div className="finder__results__card__details">
                <h3 className="card__title finder__results__card__heading">
                    {props.details.metaData.code} {props.details.title}
                </h3>
                {topic}
                {school}
                {department}
                {location}
                {specification}
            </div>
        </li>
    );
}

finder__results__module.propTypes = {
    details: PropTypes.object,
    query: PropTypes.object,
};

export default finder__results__module;
